import {
    FETCHDEVICEBYID,
    CREATEDEVICE,
    DELETEDEVICE,
    FETCHSTATUS,
    FETCHRELATEDDEVICE,
    FETCHSTATUSLIST
} from '../actions/device';

const initialState = {
    fetchedDevice: null,
    createdDevice: null,
    fetchedStatus: null,
    relatedDevices: null,
    statusList: null,
};

export default (state = initialState, action) => {
    switch (action.type) {
        case FETCHDEVICEBYID:
            return {
                ...state,
                fetchedDevice: action.fetchedDevice
            };
        case CREATEDEVICE:
            return {
                ...state,
                createdDevice: action.createdDevice
            }
        case DELETEDEVICE:
            return initialState;
        case FETCHSTATUS:
            return {
                ...state,
                fetchedStatus: action.fetchedStatus
            }
        case FETCHRELATEDDEVICE:
            return {
                ...state,
                relatedDevices: action.relatedDevices
            }
        case FETCHSTATUSLIST:
            return {
                ...state,
                statusList: action.status
            };
        default:
            return state;
    }
};