import React, { useState } from 'react';
import { Card, Result, Button, Popover } from 'antd';
import { Row, Table } from 'reactstrap';
import { useSelector, useDispatch } from 'react-redux';
import { PageSpinner } from '../BasicElement/';
import * as deviceActions from '../../store/actions/device';
import moment from 'moment';

const DeviceInfoCard = props => {

    const { deviceInfo, onDelete} = props;
    const userInfo = useSelector(state => state.user.userProfile);
    const dispatch = useDispatch();
    const [isLoading, setIsLoading] = useState(false);
    const [visible, setVisible] = useState(false);



    if (!!!userInfo || isLoading) {
        return <PageSpinner />
    }

    const handleDelete = async () => {
        setIsLoading(true);
        await dispatch(deviceActions.deletedevice(deviceInfo.system._id, deviceInfo._id))
            .then(setIsLoading(false))
            .then(onDelete());
    }

    const handleVisibleChange = visible => {
        setVisible(visible);
    };


    if (!!!deviceInfo ) {
        return (<Result
            status="404"
            title="404"
            subTitle="Sorry, the page you visited does not exist."
        />)
    }

    return (
        <Card>
            <Row className="d-flex">
                <div className="mx-8 ml-4 mr-auto">Device Info</div>
                <Popover
                    content={<Button type="danger" onClick={handleDelete} >^_^Yes</Button>}
                    title="Are you sure?"
                    trigger="click"
                    visible={visible}
                    onVisibleChange={handleVisibleChange}
                >
                    {(userInfo.role === "co" || userInfo.role === "admins") && <div className="align-self-center text-red-500 mr-4 cursor-pointer" >
                        Remove
                </div>}
                </Popover>
                <Table hover responsive className="bg-white">
                    <thead>
                        <tr>
                            <th><small>Name</small></th>
                            <th><small>Last Updated</small></th>
                            <th><small>Stats Code</small></th>
                        </tr>
                    </thead>
                    <tbody>
                        {!!deviceInfo &&
                            <tr >
                                <td>{deviceInfo.name}</td>
                                <td>{!!deviceInfo.lastUpdated ? moment(new Date(deviceInfo.lastUpdated)).format('YYYY-MM-DD') : "Not connected yet"}</td>
                                <td> {deviceInfo.statusCode}
                                </td>
                            </tr>
                        }
                    </tbody>
                </Table>
            </Row>
        </Card>
    )
}

export default DeviceInfoCard;